<template>
  <div class="search">
    <!-- 搜索框 -->
    <div class="search-box">
      <van-search
        v-model="searchValue"
        shape="round"
        placeholder="搜索歌曲、歌手、专辑"
        @search="getMultiMatchData"
      ></van-search>
    </div>

    <!-- 热门搜索 -->
    <div class="hotsearch-box" v-if="!searchValue">
      <HomeTitle>热门搜索</HomeTitle>
      <ul class="searchhot">
        <li
          ref="hotSearchs"
          v-for="(item, index) in hotSearch"
          :key="index"
          @click="clickHot(item)"
        >{{item.first}}</li>
      </ul>
    </div>

    <!-- 搜索建议 -->
    <div class="suggest-box" v-if="searchValue&&multiMatchData==false">
      <div class="suggest">
        <h5 class="emem f-bd">搜索"{{searchValue}}"</h5>
        <ul>
          <li v-for="(item,index) in someSuggest" :key="index" @click="clickSuggests(item)">
            <i></i>
            <span class="f-bd">{{item.keyword}}</span>
          </li>
        </ul>
      </div>
    </div>

    <!-- 多重匹配 -->
    <div class="multimatch" v-if="multiMatchData">
      <!-- 歌手 -->
      <div class="multi-artist" v-if="multiMatchData.artist">
        <div
          class="artist f-bd"
          v-for="(item,index) in multiMatchData.artist"
          :key="index"
          @click="goArtist(item.id)"
        >
          <img :src="item.picUrl" alt />
          <h5>
            <p>
              歌手：{{item.name}}
              <span v-for="(v, i) in item.alias" :key="i">({{v}})</span>
            </p>
            <p>
              <span>粉丝：{{translatePlayCount(item.fansSize)}}</span>
              <span>歌曲：{{item.musicSize}}</span>
              <span>专辑：{{item.albumSize}}</span>
            </p>
          </h5>
          <span class="arrow">
            <van-icon name="arrow" />
          </span>
        </div>
      </div>

      <!-- mv -->
      <div class="multi-mv" v-if="multiMatchData.mv">
        <div
          class="mv f-bd"
          v-for="(item,index) in multiMatchData.mv"
          :key="index"
          @click="goMv(item.id)"
        >
          <img :src="item.cover" alt class="auto-img" />
          <h5>
            <p>
              MV:
              <span>{{item.name}}</span>
            </p>
            <p>{{item.artistName}}</p>
          </h5>
          <span class="arrow">
            <van-icon name="arrow" />
          </span>
        </div>
      </div>

      <!-- 专辑 -->
      <div class="multi-album" v-if="multiMatchData.album">
        <div
          class="album f-bd"
          v-for="(item,index) in multiMatchData.album"
          :key="index"
          @click="goAlbum(item.id)"
        >
          <div class="piccover">
            <img :src="item.blurPicUrl" alt class="auto-img" />
          </div>

          <h5>
            <p>
              专辑：{{item.name}}
              <span v-for="(v, i) in item.alias" :key="i">({{v}})</span>
            </p>
            <p>{{item.artist.name}}</p>
          </h5>
          <span class="arrow">
            <van-icon name="arrow" />
          </span>
        </div>
      </div>
    </div>

    <!-- 歌曲列表 -->
    <ul class="songs-item">
      <SearchListItem
        v-for="(item,index) in songItems"
        :key="index"
        :newItem="item"
        @translate-currentid="$emit('translate-currentid',$event)"
      ></SearchListItem>
    </ul>
  </div>
</template>

<script>
import "../assets/less/search.less";
import HomeTitle from "../components/Hometitle";
import SearchListItem from "../components/SearchListItem";
export default {
  components: {
    HomeTitle,
    SearchListItem,
  },
  watch: {
    searchValue(value) {
      if (value) {
        this.getSuggets(value);
      } else {
        this.multiMatchData = [];
        this.songItems = [];
      }
    },
  },
  data() {
    return {
      searchValue: "",
      hotSearch: [],
      someSuggest: [],
      multiMatchData: [],
      songItems: [],
    };
  },
  created() {
    this.getHotSearch();
  },
  methods: {
    //格式化数据
    translatePlayCount(n) {
      if (n > 99999999) {
        n / 10000;
        return Number(n / 100000000).toFixed(2) + "亿";
      }
      if (n > 9999) {
        n / 10000;
        return Number(n / 10000).toFixed(1) + "万";
      }
    },
    //获取搜索的热词
    getHotSearch() {
      this.hotSearch = [];
      this.multiMatchData = [];
      this.$toast.loading({
        message: "加载中...",
        forbidClick: true,
        duration: 0,
        loadingType: "spinner",
      });
      this.axios({
        method: "GET",
        url: "http://musicapi.leanapp.cn/search/hot/detail",
      })
        .then((result) => {
          this.$toast.clear();
          
          if (result.data.code == 200) {
            this.hotSearch = result.data.result.hots;
          }
        })
        .catch((err) => {
          this.$toast.clear();
        });
    },
    //获取搜索建议
    getSuggets(value) {
      this.someSuggest = [];
      this.axios({
        method: "GET",
        url:
          "http://music.kele8.cn/search/suggest?keywords= " +
          value +
          "&type=mobile",
      })
        .then((result) => {
          //
          if (result.data.code == 200) {
            this.someSuggest = result.data.result.allMatch;
          }
        })
        .catch((err) => {});
    },
    //获取搜索数据
    getMultiMatchData() {
      this.multiMatchData = [];
      this.$toast.loading({
        message: "加载中...",
        forbidClick: true,
        duration: 0,
        loadingType: "spinner",
      });
      this.axios({
        method: "GET",
        url:
          "http://music.kele8.cn/search/multimatch?keywords=" +
          this.searchValue,
      })
        .then((result) => {
          this.$toast.clear();
          
          if (result.data.code == 200) {
            this.multiMatchData = result.data.result;
          }
          this.getSongItems();
        })
        .catch((err) => {
          this.$toast.clear();
        });
    },
    //获取搜索歌曲列表
    getSongItems() {
      this.songItems = [];
      this.axios({
        method: "GET",
        url:
          "http://music.kele8.cn/search?keywords=" +
          this.searchValue +
          "&offset=0&limit=10",
      })
        .then((result) => {
          
          if (result.data.code == 200) {
            this.songItems = result.data.result.songs;
          }
        })
        .catch((err) => {});
    },
    //点击搜索热词进行搜索
    clickHot(item) {
      //
      let hotSearch = item.first;
      //
      this.searchValue = item.first;
      this.getMultiMatchData();
    },
    //点击搜索建议词进行搜索
    clickSuggests(item) {
      
      let hotSearch = item.keyword;
      this.searchValue = item.keyword;
      this.getMultiMatchData();
    },
    //去到歌手详情页面
    goArtist(param) {
      this.$router.push({ name: "Artist", query: { id: param } });
    },
    //去到专辑详情界面
    goAlbum(param) {
      this.$router.push({ name: "Album", query: { id: param } });
    },
    //去到mv界面
    goMv(param) {
      this.$router.push({ name: "Mv", query: { id: param } });
    },
  },
};
</script>

<style lang="less" scoped>
</style>