      <div class="recommend-new-box2">
        <div class="recommend-new-title one-text">{{newItem.name}}</div>
        <div class="recommend-singer-box one-text">
          <div>
            <i class="recommend-new-singer"></i>
            {{newItem.artists[0].name}} - {{newItem.name}}
          </div>
        </div>
      </div>
      <div class="recommend-new-play">
        <span class="recommend-playbtn"></span>
      </div>
    </div>
  </li>
</template>

<script>
//https://music.163.com/song/media/outer/url?id=
//https://music.kele8.cn/song/detail
export default {
  props: ["newItem"],
  methods: {
    setCurrentSongId(id) {
      
      this.$emit("translate-currentid", {
        songId: id,
      });
    },
  },
};
</script>

